import React from "react";
import { Button } from "antd";
import { Input } from "antd";

const { Search } = Input;

const BuscadorProgramacion = ({ setFilterText, setModal }) => {
  const handleModal = () => {
    setModal(true);
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
      }}
    >
      <div
        style={{
          width: "300px",
        }}
      >
        <Search
          placeholder="Ingresa un termino aqui..."
          onChange={(e) => setFilterText(e.target.value)}
        />
      </div>
      <div>
        <Button onClick={handleModal} style={{ width: "150px" }}>
          Pago Multiple
        </Button>
      </div>
    </div>
  );
};

export default BuscadorProgramacion;
