import { Col, Empty, Row } from "antd";
import dayjs from "dayjs";
import React, { useContext, useEffect, useState } from "react";
import { AiOutlineForm } from "react-icons/ai";
import { CrudContext } from "../../../context/CrudContext";
import { PlanillaContext } from "../../../context/PlanillaContext";
import { pagosLayout } from "../../../data/dataTable";
import { notificacion } from "../../../helpers/mensajes";
import ButtonAdd from "../../Button/ButtonAdd";
import Header from "../../header/Header";
import Tabla from "../../tabla/Tabla";
import BuscadorProgramacion from "../BuscadorProgramacion";
import ModalJuntarTeletrans from "./ModalJuntarTeletrans";

const PagosLayout = () => {
  const { getData, createData } = useContext(CrudContext);
  // const { juntarTeletrans, setJuntarTeletrans } = useContext(PlanillaContext);
  const [pagos, setPagos] = useState([]);
  const [modal, setModal] = useState(false);

  const [openModalIndivudual, setOpenModalIndivudual] = useState(false);

  const handleCloseModalIndivudual = () => {
    setOpenModalIndivudual(false);
  };

  const [dataPagos, setDataPagos] = useState([]);
  const [filterText, setFilterText] = useState("");
  const [search, setSearch] = useState([]);
  const [multiples, setMultiples] = useState([]);

  const getPagos = async () => {
    const response = await getData("programacion");

    if (response) {
      const filterNull = response.data.filter((item) => item !== null);
      setPagos(filterNull);
      // Guardar valores iniciales de teletrans y volquetes en cada objeto de la fila
      const initialValues = filterNull.map((item) => ({
        ...item,
        initialTeletrans: item.teletrans,
        initialVolquetes: item.volquetes,
        fecha_pago: dayjs(new Date()).format("YYYY-MM-DD"),
      }));
      setPagos(initialValues);
    }
  };

  useEffect(() => {
    getPagos();
  }, []);

  const handleData = (event, e, i) => {
    const { name, value } = event.target;

    setPagos((state) =>
      state.map((item, index) =>
        index === i
          ? {
              ...item,
              [name]: value,
              contrato_id: e?.contrato_id,
              id: e?.pagos?.pagos?.at(-1)?.id || "",
            }
          : item
      )
    );
  };
  const postPagos = async (e) => {
    const info = {
      observacion: e.observacion,
      fecha_pago: e.fecha_pago,
      volquetes: e.initialVolquetes,
      teletrans: e.initialTeletrans,
      tipo: "pago",
      trabajadores: [
        {
          volquetes: e.initialVolquetes,
          teletrans: e.initialTeletrans,
          contrato_id: e.contrato_id,
          trabajador_dni: e.dni,
          quincena: e.quincena,
        },
      ],
    };
    const valor = e.initialVolquetes * 4 + e.initialTeletrans;
    if (valor > e.saldo) {
      notificacion(500, "El monto a pagar es mayor a lo adeudado.");
    } else if (valor % 4 === 0) {
      notificacion(
        500,
        "Error! solo se puede pagar un cantidad equivalente a un volquete."
      );
    }

    const response = await createData(info, "pago/programacion/multiple");

    if (response) {
      notificacion(response.status, response.msg);
      getPagos();
    }
  };

  useEffect(() => {
    const filtered =
      pagos &&
      pagos.filter(
        (item) =>
          item?.nombre?.toLowerCase()?.includes(filterText.toLowerCase()) ||
          item?.cargo?.toLowerCase()?.includes(filterText.toLowerCase())
      );

    setSearch(filtered);
  }, [filterText, pagos]);

  const columns = pagosLayout(handleData, postPagos);

  useEffect(() => {
    const prueba = pagos.map((item) => {
      return {
        id: item.id,
        contrato_id: item.contrato_id,
        dni: item.dni,
        nombre: item.nombre + " - " + item.quincena,
        quincena: item.quincena,
      };
    });

    if (prueba.length > 0) {
      setMultiples(prueba);
    }
  }, [pagos]);

  return (
    <>
      <Header text={"Programación de pagos"} ruta={"/planilla"} />
      <div className="margenes">
        <BuscadorProgramacion
          setModal={setModal}
          setFilterText={setFilterText}
        />

        <Tabla columns={columns} table={search} />
      </div>
      {modal && (
        <ModalJuntarTeletrans
          open={modal}
          setModal={setModal}
          closeModal={handleCloseModalIndivudual}
          actualizarTabla={getPagos}
          trabajadores={multiples}
        />
      )}
    </>
  );
};

export default PagosLayout;
